import { ChangeDetectorRef, Component, ComponentFactoryResolver, ComponentRef, ElementRef, OnInit, ViewChild, ViewContainerRef } from '@angular/core';
import { animate, state, style, transition, trigger } from '@angular/animations';

import { DatePipe, getCurrencySymbol } from '@angular/common';
import { ToastController, ModalController, LoadingController } from '@ionic/angular';
import { ApiService } from 'src/app/services/api.service';
import { DataService } from 'src/app/services/data.service';
import { LoadingService } from 'src/app/services/loading.service';
import { TimeSlotsComponent } from 'src/app/components/time-slots/time-slots.component';
import { BranchComponent } from 'src/app/components/branch/branch.component';
import { AppointmentpopupPage } from '../../appointmentpopup/appointmentpopup.page';
import { MomentDateAdapter } from '@angular/material-moment-adapter';
import { DateAdapter, MAT_DATE_FORMATS, MAT_DATE_LOCALE } from '@angular/material/core';
import { TokenV2Page } from '../../token-v2/token-v2.page';
import { ToastService } from 'src/app/services/toast.service';
import { DenominationSlideComponent } from 'src/app/components/denomination-slide/denomination-slide.component';
import { CurrencyconverterPage } from '../../currencyconverter/currencyconverter.page';
import { fromEvent } from 'rxjs';
import { debounceTime, distinctUntilChanged, filter, map } from 'rxjs/operators';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';

export const MY_FORMATS = {
  parse: {
    dateInput: 'LL',
  },
  display: {
    dateInput: 'YYYY-MM-DD',
    monthYearLabel: 'YYYY',
    dateA11yLabel: 'LL',
    monthYearA11yLabel: 'YYYY',
  },
};


@Component({
  selector: 'app-forex-cash-v2',
  templateUrl: './forex-cash-v2.page.html',
  styleUrls: ['./forex-cash-v2.page.scss'],
  animations: [
    trigger('openClose', [
      state('true', style({ height: '*' })),
      state('false', style({ height: '0px' })),
      transition('false <=> true', [animate(500)])
    ])
  ],
  providers: [
    { provide: DateAdapter, useClass: MomentDateAdapter, deps: [MAT_DATE_LOCALE] },
    { provide: MAT_DATE_FORMATS, useValue: MY_FORMATS },
  ],
})
export class ForexCashV2Page implements OnInit {
  @ViewChild("denom", { read: ElementRef }) denomButtonRef: ElementRef;
  @ViewChild("forexT", { read: ElementRef }) forexTButtonRef: ElementRef;
  @ViewChild('dynamic', { read: ViewContainerRef }) private viewRef: ViewContainerRef;
  @ViewChild('dynamicForex', { read: ViewContainerRef }) private dynamicViewRef: ViewContainerRef;


  animationState = 'out';
  selectedCountryCodeForexTrans: string = '';
  selectedCountryCode: string = '';
  isShow: boolean = true;
  toggle = false;
  frmToggle: boolean = false;
  ftmToggle: boolean = false;
  flag: boolean = true;
  public form: FormGroup;
  routerData: any;
  editMode: boolean = false;
  phoneNumber: any;
  customerId: any;
  currencies: any = [];
  selectedCurrency: any;
  customerDetails: any;
  users: any;
  curr: any;
  currentBalance: any;
  accountInfo: any;
  chargeAmount: any = 0;
  accountflag: boolean;
  isedit: boolean;
  accountDetails: any;
  trnBrn: any;
  brnflg: any;
  transAmt: any;
  nearestBrn: boolean;
  tokenCount: any;
  accBranch: string;
  transactionId: any;
  isFrmClosed: boolean = false;
  amountOfDenomination: number = 0;
  amountOfTotalDenomination: number = 0;
  isShowDenomError: boolean = false;
  isShowDenomPayRecError: boolean = false;

  isOpen: boolean = false;
  isShowRepError: boolean = false;
  depositType: any;
  homeIconToggle: boolean = false;

  @ViewChild('amountEnterInput', { static: false }) amountEnterInput: ElementRef;

  constructor(private router: Router,
    private formBuilder: FormBuilder,
    private api: ApiService,
    public toastCtrl: ToastController,
    public toastService: ToastService,
    private modalController: ModalController,
    public datepipe: DatePipe,
    public loading: LoadingService,
    private shareDataService: DataService,
    private loadingController: LoadingController,
    private cdr: ChangeDetectorRef) { }

  ngOnInit() {
    this.formInit();

    if (this.router.getCurrentNavigation() != null) {
      this.routerData = this.router.getCurrentNavigation().extras.state;
      console.log("Values?", this.routerData);
      this.editMode = this.routerData?.data ? true : false;
    }

    this.phoneNumber = localStorage.getItem('customerPhonenum');
    this.customerId = sessionStorage.getItem('customer_id');
    this.currencies = JSON.parse(localStorage.getItem('COUNTRIES'));
    console.log(this.currencies);

    if (this.editMode) {
      const select_forex_countryCode = this.currencies.filter((i: any) => i.currencyCode === this.routerData?.data?.forexTransCurrency);
      const select_countryCode = this.currencies.filter((i: any) => i.currencyCode === this.routerData?.data?.forexTransCurrency);

      this.selectedCountryCodeForexTrans = select_forex_countryCode[0].countryCode.toLowerCase();
      this.selectedCountryCode = select_countryCode[0].countryCode.toLowerCase();
      const data = this.routerData?.data;
      console.log("REP", data?.representativeInfo);
      let deposit_type = data?.representativeInfo.length != 0 ? 'representative' : 'self';
      this.depositType = deposit_type;
      this.form.get('depositType')?.patchValue(deposit_type);

      if (deposit_type == 'representative') {
        this.form.addControl('representativeInfo', this.repFG);
        this.form.get('representativeInfo').get('personName').patchValue(data?.representativeInfo?.[0]?.personName);
        this.form.get('representativeInfo').get('phoneNo').patchValue(data?.representativeInfo?.[0]?.phoneNo);
        this.form.get('representativeInfo').get('repdocumentId').patchValue(data?.representativeInfo?.[0]?.documentId);
        this.form.get('representativeInfo').get('repdocumentNumber').patchValue(data?.representativeInfo?.[0]?.documentNumber);
      } else {
        this.form.removeControl('representativeInfo');
      }

    } else {
      const countryCode = this.currencies.filter((i: any) => i.currencyCode === "USD");
      this.selectedCurrency = countryCode[0];
      this.depositType = 'self';
      this.form.get('depositType')?.patchValue('self');
    }

    this.loadData();

    this.shareDataService.getAccountInfo.subscribe(data => {
      this.accountInfo = data;
      console.log(data);
      this.api.getNumberOfCrowd(this.accountInfo.accountBranch)
        .subscribe((data1: any) => {
          this.tokenCount = data1.tokenCount || '0';
        })
    });

    this.form.get('forexAmount').valueChanges.subscribe(val => {
      if (val) {
        this.denomButtonRef?.nativeElement.classList.add('active-bg');
      }
    })

    this.form.get('forexTransAmount').valueChanges.subscribe(val => {
      if (val) {
        this.forexTButtonRef?.nativeElement.classList.add('active-bg');
      }
    })

    this.form.get('forexTransCurrency').valueChanges.subscribe(val => {
      if (val) {
        const countryCode = this.currencies.filter((i: any) => i.currencyCode === val);
        this.selectedCountryCode = countryCode[0].countryCode.toLowerCase();
      }
    })

    this.form.get('forexAmount').valueChanges.pipe(debounceTime(1500), filter(res => res > 0), distinctUntilChanged())
      .subscribe(res => {
        this.updateTransAmount();
      });


    let today = new Date().toISOString();
    this.form.get('transactionDate').patchValue(today);

    console.log(this.form.value);

  }

  formInit() {
    this.form = this.formBuilder.group({
      transactionId: [''],
      customerId: ['', [Validators.required]],
      productCode: ['FTX', [Validators.required]],
      tokenOrigin: ['Mobile', [Validators.required]],
      forexTransType: ['sale', [Validators.required]],
      forexCurrency: ['', [Validators.required]],
      forexAmount: ['', [Validators.required]],
      exchangeRate: ['', Validators.required],
      branchFlag: [true, [Validators.required]],
      accountBranch: ['', [Validators.required]],
      transactionDate: ['', [Validators.required]],
      transactionBranch: ['', [Validators.required]],
      isMobileTrans: [true, [Validators.required]],
      transactionTime: ['', [Validators.required]],
      accountAmount: ['', [Validators.required]],
      totalChargeAmount: ['0', [Validators.required]],
      documentId: ['', [Validators.required]],
      documentNumber: ['', [Validators.required]],
      depositType: ['', [Validators.required]],
      forexTransCurrency: ['', [Validators.required]],
      forexTransAmount: ['', [Validators.required]],
      totalTransactionAmount: ['0', [Validators.required]],
      remarks: ['', [Validators.required]],
      // Audit log keys not required 
      totalAmount: [''],
      createdBy: [''],
      createdTime: [''],
      modifiedBy: [''],
      modifiedTime: [''],
      recordStatus: [''],
      authStatus: [''],
      version: [''],
    })
  }

  repFG: FormGroup = this.formBuilder.group({
    personName: ['', Validators.required],
    phoneNo: ['', [Validators.required, Validators.minLength(10),
    Validators.maxLength(10)]],
    repdocumentId: [''],
    repdocumentNumber: [''],
  });

  keyPressMob(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }

  loadData() {
    this.loading.present();
    this.api.custpomerDetails(this.phoneNumber)
      .subscribe((resp) => {
        this.loading.dismiss();
        resp.custAccount = resp.custAccount.filter(card => card.status === "APPROVED");
        this.customerDetails = resp;
        this.upsertAccount(resp);
      }, (err: any) => {
        this.loading.dismiss();
      })
  }

  onScreenRefresh() {
    this.editMode = this.routerData?.data ? true : false;
    this.phoneNumber = localStorage.getItem('customerPhonenum');
    this.customerId = sessionStorage.getItem('customer_id');
    this.currencies = JSON.parse(localStorage.getItem('COUNTRIES'));

    if (this.editMode) {
      const select_forex_countryCode = this.currencies.filter((i: any) => i.currencyCode === this.routerData?.data?.forexTransCurrency);
      const select_countryCode = this.currencies.filter((i: any) => i.currencyCode === this.routerData?.data?.forexTransCurrency);

      this.selectedCountryCodeForexTrans = select_forex_countryCode[0].countryCode.toLowerCase();
      this.selectedCountryCode = select_countryCode[0].countryCode.toLowerCase();
      const data = this.routerData?.data;
      console.log("data?.representativeInfo", data?.representativeInfo);
      let deposit_type = data?.representativeInfo.length != 0 ? 'representative' : 'self';
      this.depositType = deposit_type;
      this.form.get('depositType')?.patchValue(deposit_type);
      this.form.get('forexTransType')?.patchValue(data?.forexTransType);

      if (deposit_type == 'representative') {
        this.form.addControl('representativeInfo', this.repFG);
        this.form.get('representativeInfo').get('personName').patchValue(data?.representativeInfo?.[0]?.personName);
        this.form.get('representativeInfo').get('phoneNo').patchValue(data?.representativeInfo?.[0]?.phoneNo);
        this.form.get('representativeInfo').get('repdocumentId').patchValue(data?.representativeInfo?.[0]?.documentId);
        this.form.get('representativeInfo').get('repdocumentNumber').patchValue(data?.representativeInfo?.[0]?.documentNumber);
      } else {
        this.form.removeControl('representativeInfo');
      }

    } else {
      const countryCode = this.currencies.filter((i: any) => i.currencyCode === "USD");
      this.selectedCurrency = countryCode[0];
      this.depositType = 'self';
      this.form.get('depositType')?.patchValue('self');
    }

    this.loadData();

    this.shareDataService.getAccountInfo.subscribe(data => {
      this.accountInfo = data;
      console.log(data);
      this.api.getNumberOfCrowd(this.accountInfo.accountBranch)
        .subscribe((data1: any) => {
          this.tokenCount = data1.tokenCount || '0';
        })
    });

    this.form.get('forexAmount').valueChanges.subscribe(val => {
      if (val) {
        this.denomButtonRef?.nativeElement.classList.add('active-bg');
      }
    })

    this.form.get('forexTransAmount').valueChanges.subscribe(val => {
      if (val) {
        this.forexTButtonRef?.nativeElement.classList.add('active-bg');
      }
    })

    this.form.get('forexTransCurrency').valueChanges.subscribe(val => {
      if (val) {
        const countryCode = this.currencies.filter((i: any) => i.currencyCode === val);
        this.selectedCountryCode = countryCode[0].countryCode.toLowerCase();
      }
    })

  }

  screenOneValidation() {
    let disable: boolean;
    if (this.form.get('forexCurrency')?.valid &&
      this.form.get('forexAmount')?.valid &&
      this.form.get('forexTransCurrency')?.valid &&
      this.form.get('forexTransAmount')?.valid &&
      this.form.get('documentId')?.valid &&
      this.form.get('documentNumber')?.valid
    ) {
      disable = false;
    } else {
      disable = true;
    }
    return disable
  }

  screenTwoValidation() {
    let disable: boolean;
    if (this.form.get('transactionBranch')?.valid &&
      this.form.get('transactionDate')?.valid &&
      this.form.get('transactionTime')?.valid

    ) {
      disable = false;
    } else {
      disable = true;
    }
    return disable
  }

  screenDynamicControlValidation() {
    let disable: boolean;
    if (this.form.get('transactionBranch')?.valid &&
      this.form.get('transactionDate')?.valid &&
      this.form.get('transactionTime')?.valid &&
      this.form.get('representativeInfo')?.get('personName')?.valid &&
      this.form.get('representativeInfo')?.get('phoneNo')?.valid &&
      this.form.get('representativeInfo')?.get('repdocumentId')?.valid &&
      this.form.get('representativeInfo')?.get('repdocumentNumber')?.valid
    ) {
      disable = false;
    } else {
      disable = true;
    }
    return disable
  }

  getHomeBranch() {
    this.homeIconToggle = !this.homeIconToggle;
    if (this.homeIconToggle) {

      this.form.get('transactionBranch')?.patchValue(this.customerDetails?.custAccount[0]?.accountBranch);
    } else {
      this.form.get('transactionBranch')?.patchValue("");
    }
    console.log("Clicked", this.form.get('transactionBranch')?.value);
  }

  createComponent() {
    this.viewRef.clear();
    this.dynamicViewRef.clear();
    let childComponentRef = this.viewRef.createComponent(DenominationSlideComponent);
    let childComponent = childComponentRef.instance;
    childComponent.unique_key = "viewRef";
    childComponent.parentRef = this;
  }

  removeDynamicComponent(): void {
    this.viewRef.clear();
  }

  createComponentOne() {
    this.dynamicViewRef.clear();
    this.viewRef.clear();

    let childComponentRef = this.dynamicViewRef.createComponent(DenominationSlideComponent);
    let childComponent = childComponentRef.instance;
    childComponent.unique_key = "dynamicViewRef";
    childComponent.parentRef = this;
  }

  removeDynamicComponentOne(): void {
    this.dynamicViewRef.clear();
  }

  upsertAccount(filteredResponseSavingAccount: any) {

    this.users = filteredResponseSavingAccount.custAccount;
    this.customerId = filteredResponseSavingAccount.customerId;
    this.curr = getCurrencySymbol(filteredResponseSavingAccount.custAccount[0].accountCurrency, "narrow");

    if (this.editMode) {
      const data = this.routerData?.data;
      this.form.get('forexTransType')?.patchValue(data?.forexTransType);
      this.form.get('forexCurrency')?.patchValue(data?.forexCurrency);
      this.form.get('forexAmount')?.patchValue(data?.forexAmount);
      this.form.get('forexTransCurrency')?.patchValue(data?.forexTransCurrency);
      this.form.get('forexTransAmount')?.patchValue(data?.forexTransAmount);
      this.form.get('documentId')?.patchValue(data?.documentId);
      this.form.get('documentNumber')?.patchValue(data?.documentNumber);
      this.form.get('transactionBranch').patchValue(data?.transactionBranch);
      this.form.get('transactionDate').patchValue(data?.transactionDate);
      this.form.get('transactionTime').patchValue(data?.transactionTime);
      this.form.get('remarks').patchValue(data?.remarks);
      this.getChargeDetails();
    } else {
      this.accountflag = this.users.length == 1 ? true : false;
      this.selectedCountryCode = this.selectedCurrency.countryCode.toLowerCase();
      this.form.get('forexCurrency')?.patchValue(this.selectedCurrency.currencyCode);
      this.form.controls.accountBranch.patchValue(filteredResponseSavingAccount.custAccount[0].accountBranch);
    }

    this.cdr.markForCheck();
  }

  getChargeDetails() {
    this.api.getChargeDetails(this.accountInfo.accountId).subscribe(resp => {
      this.chargeAmount = resp?.[0]?.amount;
      this.form.get('totalChargeAmount').patchValue(resp?.[0]?.amount);
    }, err => console.log('Error: ', err))
  }

  get f() { return this.form.controls; }

  compareWith(o1, o2) {
    return o1 && o2 ? o1.id === o2.id : o1 === o2;
  }

  goToCurrencyConverter() {
    this.router.navigate(['currencyconverter']);
  }

  onCountrySelected(e: any) {
    console.log(e);
  }

  openPopup() {
    this.modalController.create({
      component: TimeSlotsComponent,
      componentProps: {
        date: this.form.get('transactionDate').value,
      }
    }).then(modalResp => {
      modalResp.present()
      modalResp.onDidDismiss().then(res => {
        if (res.data != null) {
          console.log(res);
          this.form.get('transactionTime').patchValue(res.data);
        }
      })
    })
  }

  selectForexCurrencyCode(e: any) {
    console.log(typeof e);
    if (typeof e !== "undefined") {
      const filteredCurrency = this.currencies?.filter((i: any) => i.currencyCode === e);
      this.selectedCountryCodeForexTrans = filteredCurrency[0].countryCode.toLowerCase();
      this.cdr.markForCheck();
    } else {
      return;
    }
  }

  selectCurrencyCode(e: any) {
    console.log(typeof e);
    if (typeof e !== "undefined") {
      const filteredCurrency = this.currencies?.filter((i: any) => i.currencyCode === e);
      this.selectedCountryCode = filteredCurrency[0].countryCode.toLowerCase();
      this.cdr.markForCheck();

      let fromCurrency = this.form.get('forexCurrency')?.value
      let toCurrency = this.form.get('forexTransCurrency')?.value
      let get_amount = Number(this.form.get('forexAmount')?.value);

      if (fromCurrency != undefined && toCurrency != undefined && get_amount != undefined) {
        this.getExChangeRate(this.form.get('forexCurrency').value, filteredCurrency[0].currencyCode);
      }
    } else {
      return;
    }
  }

  getExChangeRate(fromCurr: string, toCurr: string) {
    console.log("fromCurr, tocurr", fromCurr, toCurr);
    // this.api.getNativeExchangeRate(fromCurr, toCurr).subscribe((res: any) => {
    //   const rate = this.form.get('forexTransType').value == 'sale' ? res[0].sellRate : res[0].buyRate;
    //   console.log("Rate?", rate);
    //   this.form.get('exchangeRate').patchValue(rate);
    //   let updatedAmount: number = (Number(this.form.get('forexAmount')?.value) * Number(this.form.get('exchangeRate')?.value)) + this.chargeAmount;
    //   if (!isNaN(Number(updatedAmount))) {
    //     console.log(Number(updatedAmount));
    //     this.form.get('forexTransAmount').patchValue(updatedAmount);
    //     // this.form.get('totalTransactionAmount').patchValue(updatedAmount);
    //     this.cdr.markForCheck();
    //   }
    // });
    this.updateTransAmount();
  }

  updateTransAmount() {
    // let updatedAmount: number = (Number(this.form.get('forexAmount')?.value) * Number(this.form.get('exchangeRate')?.value)) + this.chargeAmount;
    // if (!isNaN(Number(updatedAmount))) {
    //   console.log(Number(updatedAmount));
    //   this.convert();
    //   this.form.get('forexTransAmount').patchValue(updatedAmount);

    //   this.cdr.markForCheck();
    // }
    this.convert();
  }

  async convert() {

    let fromCurrency = this.form.get('forexCurrency')?.value
    let toCurrency = this.form.get('forexTransCurrency')?.value
    let get_amount = Number(this.form.get('forexAmount')?.value);

    if (fromCurrency != undefined && toCurrency != undefined && get_amount != undefined) {
      const loading = await this.loadingController.create({
        message: 'Fetching exchange rate'
      });

      loading.present();
      this.api.fetchRatesByExternal(fromCurrency, toCurrency, get_amount)
        .subscribe(data => {
          console.log("Sync ", data);
          if (data?.success) {
            loading.dismiss();
            this.form.get('exchangeRate').patchValue(data?.info?.rate);
            this.form.get('forexTransAmount').patchValue(data?.result);
          } else {
            loading.dismiss();
          }
        }, err => {
          loading.dismiss();
        });
    }
  }

  numberOnlyValidation(event: any) {
    this.transAmt = event.target.value;
    this.transAmt = this.transAmt.toString().replace(/,/g, '');
    if (parseFloat(this.currentBalance) < parseFloat(this.transAmt)) {
      this.showToast();
    } else {
      return;
    }
  }

  async showToast() {
    const toast = await this.toastCtrl.create({
      message: 'Transaction Amount should not exceed than Account Balance',
      duration: 2000,
      position: 'bottom'
    });
    toast.present();
  }

  valueSet(currentBalance) {
    this.currentBalance = currentBalance;
  }

  enableDisableAction(type: any) {
    switch (type) {
      case 'FRM':
        this.toggleFrm();
        break;
      case 'FTM':
        this.toggleFtm();
        break;
      default:
        console.log("No such type exists!");
        break;
    }
  }

  toggleFrm() {
    this.frmToggle = !this.frmToggle;
    this.animationState = this.animationState === 'out' ? 'in' : 'out';
    this.createComponent();
  }

  toggleFtm() {
    this.ftmToggle = !this.ftmToggle;
    this.animationState = this.animationState === 'out' ? 'in' : 'out';
    this.createComponentOne();
  }

  goToNextPage() {
    let dataOne = JSON.parse(localStorage.getItem('viewRef')) || null;
    let dataTwo = JSON.parse(localStorage.getItem('dynamicViewRef')) || null;

    if (dataOne != null && dataOne.total != 0) {
      if (Number(this.form.get('forexAmount')?.value) != Number(dataOne.total)) {
        this.isShowDenomError = true;
        this.toastService.showToast("Denomination total should match with the Pay/Receive currency amount!");
        return;
      } else {
        this.isShowDenomError = false;
        this.flag = false;
      }
    } else if (dataTwo != null && dataTwo.total != 0) {
      if (Number(this.form.get('forexTransAmount')?.value) != Number(dataOne.total)) {
        this.isShowDenomError = true;
        this.toastService.showToast("Denomination total should match with the Pay/Receive currency amount!");
        return;
      } else {
        this.isShowDenomError = false;
        this.flag = false;
      }
    } else {
      this.flag = false;
    }
  }

  goToPreviousPage() {
    this.flag = true;
  }

  goBack() {
    this.router.navigate(['dashboard']);
  }

  async openModalCurrency() {
    let payload = {
      fromCurrency: this.form.get('forexCurrency')?.value,
      toCurrency: this.form.get('forexTransCurrency')?.value,
      amount: this.form.get('forexAmount')?.value,
      exchangeRate: this.form.get('exchangeRate')?.value,
      total: this.form.get('forexTransAmount')?.value,
    };
    const modal = await this.modalController.create({
      component: CurrencyconverterPage,
      componentProps: {
        model_data: payload
      }
    });

    modal.onDidDismiss().then((modelData: any) => {
      if (modelData !== null) {
        console.log('Modal Data : ' + modelData);
      }
    });
    return await modal.present();
  }


  submit(form) {

    form.transactionDate.toString();
    console.log(form.transactionDate);

    var date = new Date(form.transactionDate);
    let latest_date = this.datepipe.transform(date, 'yyyy-MM-dd');
    form.transactionDate = latest_date;
    form.accountCurrency = this.accountDetails?.accountCurrency || "";
    form.customerId = this.customerId;

    if (this.editMode) {
      form.appointmentId = this.routerData.data.appointmentId;
      form.operation = 'update';
    }

    if (this.depositType == 'representative') {
      form.representativeInfo = [{
        phoneNo: this.form.get('representativeInfo').get('phoneNo')?.value,
        personName: this.form.get('representativeInfo').get('personName')?.value,
        documentId: this.form.get('representativeInfo').get('repdocumentId')?.value,
        documentNumber: this.form.get('representativeInfo').get('repdocumentNumber')?.value
      }]
    } else {
      form.representativeInfo = []
    }

    form.isMobileTrans = true;

    console.log(form);

    if (this.editMode) {
      this.api.appointmentBooking(form).subscribe(resp => {
        this.transactionId = resp.transactionId;
        localStorage.setItem('AppointmentDetails', JSON.stringify(resp));
        if (resp === 200 || resp !== null) {
          this.shareDataService.shareTransactionId(this.transactionId);
          this.form.reset();
          this.onClick(resp);
        }
      })
    } else {
      this.api.cashDepositSave(form).subscribe((resp) => {
        this.transactionId = resp.transactionId;
        localStorage.setItem('AppointmentDetails', JSON.stringify(resp));
        if (resp === 200 || resp !== null) {
          this.shareDataService.shareTransactionId(this.transactionId);
          this.form.reset();
          this.onClick(resp);
        }
      });
    }
  }

  segmentChanged(e: any) {
    if (e == 'representative') {
      this.form.addControl('representativeInfo', this.repFG);
    } else {
      this.form.removeControl('representativeInfo');
    }
    console.log(this.form.value);
  }

  goToHomepage() {
    this.router.navigate(['/dashboard']);
  }

  async onClick(event) {
    let modal = await this.modalController.create({
      component: TokenV2Page,
      componentProps: {
        value: event,
      },
    });

    modal.onDidDismiss().then((res) => {
      this.routerData = res;
      console.log("Getting Back Data", this.routerData);
      this.flag = true;
      this.onScreenRefresh();
    });

    return await modal.present();
  }

  async presentModal() {
    const modal = await this.modalController.create({
      component: BranchComponent,
      id: "branchModal",
      componentProps: {
      }
    });

    modal.onDidDismiss().then((modelData) => {
      if (modelData !== null) {
        let branch = modelData.data;
        this.tokenCount = modelData.data.data.tokenCount;
        this.form.get('transactionBranch').patchValue(modelData.data['data'].branchName);
      }
    });

    return await modal.present();
  }

  remove(view: any) {
    console.log("view disiss?", view);
    let data = JSON.parse(localStorage.getItem(view)) || null;
    console.log("Data??", data);
    if (view == 'viewRef') {
      this.viewRef.clear();
      if (data != null && data.total != 0) {
        this.denomAmountValidation(data.total);
        console.log(this.form.value);
      }
    } else {
      this.dynamicViewRef.clear();
      if (data != null && data.total != 0) {
        this.denomAmountPayAndRecValidation(data.total);
        console.log(this.form.value);
      }
    }
  }

  denomAmountValidation(amount: any) {
    if (Number(this.form.get('forexAmount')?.value) != Number(amount)) {
      this.isShowDenomError = true;
      this.toastService.showToast("Denomination total should match with the Pay/Receive currency amount");
    } else {
      this.isShowDenomError = false;
    }
    this.cdr.markForCheck();
  }

  denomAmountPayAndRecValidation(amount: any) {
    if (Number(this.form.get('forexTransAmount')?.value) != Number(amount)) {
      this.isShowDenomPayRecError = true;
      this.toastService.showToast("Denomination total should match with the Pay/Receive currency amount");
    } else {
      this.isShowDenomPayRecError = false;
    }
    this.cdr.markForCheck();
  }


  // Only Integer Numbers
  keyPressNumbers(event) {
    var charCode = (event.which) ? event.which : event.keyCode;
    // Only Numbers 0-9
    if ((charCode < 48 || charCode > 57)) {
      event.preventDefault();
      return false;
    } else {
      return true;
    }
  }

  ngOnDestroy(): void {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
    localStorage.removeItem("viewRef");
    localStorage.removeItem("dynamicViewRef");
  }

}
